import {withRouter} from 'next/router'
import {Button} from "antd";

const A = ({router, name}) => <Button>A{ router.query.id } {name}</Button>

A.getInitialProps = async () => {
    const promise = new Promise((resolve) => {
        setTimeout(() => {
            resolve({
                name: 'hello'
            })
        })
    })
    return {
        name: 'hello'
    }
}

export default withRouter(A)